export class HSB {
  public observer: Observer<this>;
  public observable: ConnectableObservable<this> = Observable.create(observer => this.observer = observer).publish();
  public bindingFlag: boolean = true;
  public hsl: string;
  public hSlider: string = "";
  public sSlider: string = "";
  public bSlider: string = "";
  public sb: string = "";
  public hb: string = "";
  public hs: string = "";

  private _h: number = 0;
  public get h(): number {
    return this._h;
  }
  public set h(h: number) {
    if (h > 360) h = 360;
    else if (h < 0) h = 0;
    if (h == this._h) return;
    this._h = h;
    if (this.observer) this.observer.next(this);
  }

  private _s: number = 100;
  public get s(): number {
    return this._s;
  }
  public set s(s: number) {
    if (s > 100) s = 100;
    else if (s < 0) s = 0;
    if (s == this._s) return;
    this._s = s;
    if (this.observer) this.observer.next(this);
  }

  private _b: number = 100;
  public get b(): number {
    return this._b;
  }
  public set b(b: number) {
    if (b > 100) b = 100;
    else if (b < 0) b = 0;
    if (b == this._b) return;
    this._b = b;
    if (this.observer) this.observer.next(this);
  }

  public updateSlider() {
    this.hSlider = `
      -webkit-linear-gradient(left,
        ${HSB.toHSL(36 * 0, this._s, this._b)},
        ${HSB.toHSL(36 * 1, this._s, this._b)},
        ${HSB.toHSL(36 * 2, this._s, this._b)},
        ${HSB.toHSL(36 * 3, this._s, this._b)},
        ${HSB.toHSL(36 * 4, this._s, this._b)},
        ${HSB.toHSL(36 * 5, this._s, this._b)},
        ${HSB.toHSL(36 * 6, this._s, this._b)},
        ${HSB.toHSL(36 * 7, this._s, this._b)},
        ${HSB.toHSL(36 * 8, this._s, this._b)},
        ${HSB.toHSL(36 * 9, this._s, this._b)},
        ${HSB.toHSL(36 * 10, this._s, this._b)}
      )`.replace(/\s+/ig, " ");
    this.sSlider = `
      -webkit-linear-gradient(left,
        ${HSB.toHSL(this._h, 0, this._b)},
        ${HSB.toHSL(this._h, 100, this._b)}
      )`;
    this.bSlider = `
      -webkit-linear-gradient(left,
        ${HSB.toHSL(this._h, this._s, 0)},
        ${HSB.toHSL(this._h, this._s, 100)}
      )`;

    this.sb = `
      -webkit-linear-gradient(top,
        ${HSB.toHSL(this._h, 0, 0, 0)},
        ${HSB.toHSL(this._h, 100, 0, 1)}
      ),
      -webkit-linear-gradient(left,
        ${HSB.toHSL(this._h, 100, 0, 0)},
        ${HSB.toHSL(this._h, 100, 100)}
      )
    `.replace(/\s+/ig, " ");

    this.hb = `
      -webkit-linear-gradient(top,
        ${HSB.toHSL(0, this._s, 100, 0)},
        ${HSB.toHSL(0, this._s, 0, 1)}
      ),
      -webkit-linear-gradient(left,
        ${HSB.toHSL(36 * 0, this._s, 100, 1)},
        ${HSB.toHSL(36 * 1, this._s, 100, 1)},
        ${HSB.toHSL(36 * 2, this._s, 100, 1)},
        ${HSB.toHSL(36 * 3, this._s, 100, 1)},
        ${HSB.toHSL(36 * 4, this._s, 100, 1)},
        ${HSB.toHSL(36 * 5, this._s, 100, 1)},
        ${HSB.toHSL(36 * 6, this._s, 100, 1)},
        ${HSB.toHSL(36 * 7, this._s, 100, 1)},
        ${HSB.toHSL(36 * 8, this._s, 100, 1)},
        ${HSB.toHSL(36 * 9, this._s, 100, 1)},
        ${HSB.toHSL(36 * 10, this._s, 100, 1)}
      )
    `.replace(/\s+/ig, " ");

    this.hs = `
      -webkit-linear-gradient(left,
        ${HSB.toHSL(36 * 0, 100, this._b, 1)},
        ${HSB.toHSL(36 * 1, 100, this._b, 1)},
        ${HSB.toHSL(36 * 2, 100, this._b, 1)},
        ${HSB.toHSL(36 * 3, 100, this._b, 1)},
        ${HSB.toHSL(36 * 4, 100, this._b, 1)},
        ${HSB.toHSL(36 * 5, 100, this._b, 1)},
        ${HSB.toHSL(36 * 6, 100, this._b, 1)},
        ${HSB.toHSL(36 * 7, 100, this._b, 1)},
        ${HSB.toHSL(36 * 8, 100, this._b, 1)},
        ${HSB.toHSL(36 * 9, 100, this._b, 1)},
        ${HSB.toHSL(36 * 10, 100, this._b, 1)}
      ),
      -webkit-linear-gradient(top,
        ${HSB.toHSL(0, 100, this._b, 0)},
        ${HSB.toHSL(0, 0, this._b, 1)}
      )
    `.replace(/\s+/ig, " ");
    this.hsl = this.toHSL();
  }

  static toHSL(h, s, b, a = 1) {
    s /= 100;
    b /= 100;
    let l = (2 - s) * b / 2;
    s = l && l < 1 ? s * b / (l < 0.5 ? l * 2 : 2 - l * 2) : s;
    return `hsla(${h}, ${s * 100}%, ${l * 100}%, ${a})`;
  }

  static toRGB(h, s, b) {
    s /= 100;
    b /= 100;
    var R, G, B, X, C;
    h = (h % 360) / 60;
    C = b * s;
    X = C * (1 - Math.abs(h % 2 - 1));
    R = G = B = b - C;

    h = ~~h;
    R += [C, X, 0, 0, X, C][h];
    G += [X, C, C, X, 0, 0][h];
    B += [0, 0, X, C, C, X][h];
    return {
      r: Math.round(R * 255),
      g: Math.round(G * 255),
      b: Math.round(B * 255)
    };
  }
  toRGB() {
    return HSB.toRGB(this.h, this.s, this.b);
  }
  toHSL() {
    return HSB.toHSL(this.h, this.s, this.b);
  }

  constructor() {
    this.updateSlider();
    this.observable.connect();
    this.observable.debounceTime(10).subscribe(hsl => hsl.updateSlider());
  }
}

export class RGB {
  public observer: Observer<this>;
  public observable: ConnectableObservable<this> = Observable.create(observer => this.observer = observer).publish();
  public bindingFlag: boolean = true;
  public rgb: string;
  // public hex: string;
  public rSlider: string = "";
  public gSlider: string = "";
  public bSlider: string = "";
  public gb: string = "";
  public rb: string = "";
  public rg: string = "";
  public hexError: boolean = false;

  private _r: number = 0;
  public get r(): number {
    return this._r;
  }
  public set r(r: number) {
    if (r > 255) r = 255;
    else if (r < 0) r = 0;
    if (r == this._r) return;
    this._r = r;
    if (this.observer) this.observer.next(this);
  }

  private _g: number = 100;
  public get g(): number {
    return this._g;
  }
  public set g(g: number) {
    if (g > 255) g = 255;
    else if (g < 0) g = 0;
    if (g == this._g) return;
    this._g = g;
    if (this.observer) this.observer.next(this);
  }

  private _b: number = 50;
  public get b(): number {
    return this._b;
  }
  public set b(b: number) {
    if (b > 255) b = 255;
    else if (b < 0) b = 0;
    if (b == this._b) return;
    this._b = b;
    if (this.observer) this.observer.next(this);
  }

  public updateSlider() {
    this.rSlider = `-webkit-linear-gradient(left, rgb(0, ${this._g}, ${this._b}) 0%, rgb(255, ${this._g}, ${this._b}) 100%)`;
    this.gSlider = `-webkit-linear-gradient(left, rgb(${this._r}, 0, ${this._b}) 0%, rgb(${this._r}, 255, ${this._b}) 100%)`;
    this.bSlider = `-webkit-linear-gradient(left, rgb(${this._r}, ${this._g}, 0) 0%, rgb(${this._r}, ${this._g}, 255) 100%)`;
    this.gb = `-webkit-linear-gradient(bottom, rgb(${Math.round(this._r / 2)}, 0, 0) 0%, rgb(${Math.round(this._r / 2)}, 255, 0) 100%), -webkit-linear-gradient(left, rgb(${Math.round(this._r / 2)}, 0, 0) 0%, rgb(${Math.round(this._r / 2)}, 0, 255) 100%)`;
    this.rb = `-webkit-linear-gradient(bottom, rgb(0, ${Math.round(this._g / 2)}, 0) 0%, rgb(255, ${Math.round(this._g / 2)}, 0) 100%), -webkit-linear-gradient(left, rgb(0, ${Math.round(this._g / 2)}, 0) 0%, rgb(0, ${Math.round(this._g / 2)}, 255) 100%)`;
    this.rg = `-webkit-linear-gradient(bottom, rgb(0, 0, ${Math.round(this._b / 2)}) 0%, rgb(0, 255, ${Math.round(this._b / 2)}) 100%), -webkit-linear-gradient(left, rgb(0, 0, ${Math.round(this._b / 2)}) 0%, rgb(255, 0, ${Math.round(this._b / 2)}) 100%)`;
    this.rgb = `rgb(${this._r}, ${this._g}, ${this._b})`;
  }

  constructor() {
    this.updateSlider();
    this.observable.connect();
    this.observable
      .debounceTime(10)
      .subscribe(rgb => {
        rgb.updateSlider();
        this.hex = this.toHex();
      });
  }

  static toHSB(r, g, b) {
    r /= 255;
    g /= 255;
    b /= 255;

    var H, S, V, C;
    V = Math.max(r, g, b);
    C = V - Math.min(r, g, b);
    H = (C === 0 ? null :
      V === r ? (g - b) / C :
        V === g ? (b - r) / C + 2 :
          (r - g) / C + 4
    );
    H = ((H + 360) % 6) * 60;
    S = C === 0 ? 0 : C / V;
    return { h: Math.round(H), s: Math.round(S * 100), b: Math.round(V * 100) };
  }

  toHSB() {
    return RGB.toHSB(this.r, this.g, this.b);
  }

  private _hex: string;
  public hexChange: EventEmitter<string> = new EventEmitter();
  public get hex() {
    return this._hex;
  }
  public set hex(hex: string) {
    this.hexError = false;
    if (this._hex === hex) { return; }
    this._hex = hex;
    if (hex.match(/^\#[A-Z0-9]{6}$/ig)) {
      this._r = parseInt(hex.substr(1, 2), 16);
      this._g = parseInt(hex.substr(3, 2), 16);
      this._b = parseInt(hex.substr(5, 2), 16);
    } else {
      this.hexError = true;
      return;
    }
    if (this.observer) this.observer.next(this);
    this.hexChange.emit(this._hex);
  }

  static leftPad(str, len, ch = ' ') {
    while (str.length < len) str = ch + str;
    return str;
  }

  static toHex(r, g, b) {
    return ("#" + RGB.leftPad(r.toString(16), 2, '0') + RGB.leftPad(g.toString(16), 2, '0') + RGB.leftPad(b.toString(16), 2, '0')).toUpperCase();
  }

  public toHex() {
    return RGB.toHex(this._r, this._g, this._b);
  }
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}
